const handleInitalScroll = (fpApi) => {
  document.querySelectorAll('.normalScroll').forEach(e => {
    const { initalScroll, fp } = e.dataset
    if (initalScroll) {
      e.scroll(e.scrollWidth * initalScroll, 0)
    }
    if (fp) {
      const touches = {
        "touchstart": { "x": -1, "y": -1 },
        "touchmove": { "x": -1, "y": -1 },
        "touchend": false,
        "direction": "undetermined"
      }
      const touchHandler = eventType => event => {
        let touch;
        if (typeof event !== 'undefined') {

          // event.preventDefault();
          if (typeof event.touches !== 'undefined') {
            touch = event.touches[0];
            switch (eventType) {
              case 'touchstart':
              case 'touchmove':
                touches[eventType].x = touch.pageX;
                touches[eventType].y = touch.pageY;
                break;
              case 'touchend':
                touches[eventType] = true;
                if (touches.touchstart.y > -1 && touches.touchmove.y > -1 && Math.abs(touches.touchstart.y - touches.touchmove.y) > window.innerHeight * 0.15) {
                  touches.direction = touches.touchstart.y < touches.touchmove.y ? "Up" : "Down";

                  // DO STUFF HERE
                  if (fpApi) {
                    fpApi[`moveSection${touches.direction}`]()
                  }
                }
              default:
                break;
            }
          }
        }
      }
      e.ontouchstart = touchHandler('touchstart')
      e.ontouchmove = touchHandler('touchmove')
      e.ontouchend = touchHandler('touchend')
    }
  })
}

const IndexPage = ({ location }) => {
  const fpApi = useRef()
  const [show, setShow] = useState()
  const [loaded, setLoaded] = useState()
  const { setHeaderColor } = useHeader()
  useEffect(() => setTimeout(() => setShow(false), 1000), [show])

  useEffect(() => {
    if (fpApi.current) {
      fpApi.current.moveTo(location.hash.substr(1))
    }
  }, [location.hash])
  return (
    <>
      {/* <motion.div
        animate={{ x: [0, 100, 0] }}
        initial={false}
      >
        <Box bg="white" p="2em">我是波浪</Box>
      </motion.div> */}
      <ReactFullpage
        anchors={anchors}
        //fullpage options
        licenseKey={process.env.GATSBY_FULLPAGE_JS_KEY}
        // onLeave={(origin, destination, direction) => {
        //   console.log(origin)
        //   //after leaving section 2
        //   if(origin.index == 1 && direction =='down'){
        //     fpApi.current.setAllowScrolling(false, 'down')
        //   }
        // }}
        afterRender={() => {
          handleInitalScroll(fpApi.current)
        }}
        afterResize={() => {
          handleInitalScroll(fpApi.current)
        }}
        scrollingSpeed={1000}
        normalScrollElements=".normalScroll"
        render={({ state, fullpageApi }) => {
          fpApi.current = fullpageApi
          if (fullpageApi?.getActiveSection().index > 6 && fullpageApi?.getActiveSection().index < 11) {
            setHeaderColor('black')
          } else {
            setHeaderColor('white')
          }
          return (
            <ReactFullpage.Wrapper>
              <CoverPage onLoad={() => setLoaded(true)} fullpageApi={fullpageApi} isActive={fullpageApi?.getActiveSection().index == 0} />
              {pages.map((Comp, i) => (
                <Comp key={i} fullpageApi={fullpageApi} isActive={fullpageApi?.getActiveSection().index == (i + 1)} anchor={anchors[i + 1]} />
              ))}
              {/* <Preface fullpageApi={fullpageApi} anchor="preface" />
              <Intro index={3} fullpageApi={fullpageApi} anchor="intro" />
              <Animals anchor="animals" fullpageApi={fullpageApi} isActive={fullpageApi?.getActiveSection().index == 3}/>
              <Care anchor="care" fullpageApi={fullpageApi} />
              <IntroPlan anchor="plan" fullpageApi={fullpageApi} />
              <Data /> */}
            </ReactFullpage.Wrapper>
          );
        }}
      />
      <AnimatePresence>
        {!loaded && (
          <Loading />
        )}
      </AnimatePresence>
    </>
  )
}

export default IndexPage
