import React, { useEffect, useRef } from 'react'
import { Box } from '@chakra-ui/react'
import loadable from '@loadable/component'

const PIXILoadable = loadable.lib(() => import('pixi.js'))
const Particles = loadable.lib(() => import('@pixi/particle-emitter'))

import bubble from '../images/bubble.png'

class ParticleEmitter {
  constructor(PIXI, canvas, imagePaths, config) {
    // Basic PIXI Setup
    const rendererOptions = {
      width: canvas.width,
      height: canvas.height,
      view: canvas,
      transparent: true,
    };
    this.stage = new PIXI.Container();
    this.emitter = null;
    this.renderer = new PIXI.Renderer(rendererOptions);
    // this.bg = null;

    // Calculate the current time
    let elapsed = Date.now();
    let updateId;

    // Update function every frame
    const update = () => {
      // Update the next frame
      updateId = requestAnimationFrame(update);

      const now = Date.now();
      if (this.emitter) {
        // update emitter (convert to seconds)
        this.emitter.update((now - elapsed) * 0.001);
      }

      elapsed = now;

      // render the stage
      this.renderer.render(this.stage);
    };

    // Resize the canvas to the size of the window
    window.onresize = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
      this.renderer.resize(canvas.width, canvas.height);
      // if (this.bg) {
      //   // bg is a 1px by 1px image
      //   this.bg.scale.x = canvas.width;
      //   this.bg.scale.y = canvas.height;
      // }
    };
    window.onresize();

    // Preload the particle images and create PIXI textures from it
    let urls;
    if (imagePaths.spritesheet) {
      urls = [imagePaths.spritesheet];
    }
    else if (imagePaths.textures) {
      urls = imagePaths.textures.slice();
    }
    else {
      urls = imagePaths.slice();
    }
    // const loader = PIXI.Loader.shared;
    // for (let i = 0; i < urls.length; ++i) {
    //   loader.add('img' + i, urls[i]);
    // }
    const emitterContainer = new PIXI.Container()
    this.stage.addChild(emitterContainer);

    this.emitter = new PIXI.particles.Emitter(
      emitterContainer,
      config,
    );

    // Center on the stage
    // this.emitter.updateOwnerPos(window.innerWidth / 2, window.innerHeight / 2);

    // Click on the canvas to trigger
    // canvas.addEventListener('mouseup', (e) => {
    //   if (!this.emitter) return;

    //   this.emitter.emit = true;
    //   this.emitter.resetPositionTracking();
    //   this.emitter.updateOwnerPos(e.offsetX || e.layerX, e.offsetY || e.layerY);
    // });

    // Start the update
    update();

    return () => {
      cancelAnimationFrame(updateId);
      this.emitter.destroy();
      this.emitter = null;
      this.stage.destroy();
      this.stage = null;
      this.renderer.destroy();
      this.renderer = null;
    }
  }
}

const Particle = ({ PIXI }) => {
  const container = useRef()
  useEffect(() => new ParticleEmitter(
    PIXI,
    container.current,
    // The image to use
    [bubble],

    // Emitter configuration, edit this to change the look
    // of the emitter
    {
      "lifetime": {
        "min": 2,
        "max": 3
      },
      "frequency": 0.08,
      "emitterLifetime": 0,
      "maxParticles": 500,
      "addAtBack": false,
      "pos": {
        "x": 0,
        "y": 0
      },
      "behaviors": [
        {
          "type": "alpha",
          "config": {
            "alpha": {
              "list": [
                {
                  "time": 0,
                  "value": 1
                },
                {
                  "time": 2,
                  "value": 0.1
                }
              ]
            }
          }
        },
        {
          "type": "moveSpeed",
          "config": {
            "speed": {
              "list": [
                {
                  "time": 0,
                  "value": 500
                },
                {
                  "time": 1,
                  "value": 200
                }
              ]
            }
          }
        },
        {
          "type": "scale",
          "config": {
            "scale": {
              "list": [
                {
                  "time": 0,
                  "value": 0.3
                },
                {
                  "time": 1,
                  "value": 0.6
                }
              ]
            },
            "minMult": 0.5
          }
        },
        {
          "type": "rotation",
          "config": {
            "accel": 0,
            "minSpeed": 0,
            "maxSpeed": 20,
            "minStart": 267,
            "maxStart": 273
          }
        },
        {
          "type": "textureRandom",
          "config": {
            "textures": [
              PIXI.Texture.from(bubble)
            ]
          }
        },
        {
          type: 'spawnShape',
          config: {
            type: 'rect',
            data: {
              x: 0,
              y: window.innerHeight,
              w: window.innerWidth,
              h: 20,
            }
          }
        }
      ]
    }
  ), [])
  return (
    <Box w="full" h="full" as="canvas" ref={container} />
  )
}

const Bubbles = ({ isActive }) => (
  <Box left={0} top={0} w="full" h="full" pos="absolute">
    <PIXILoadable>
      {(PIXI) => (
        <Particles>
          {(particles) => isActive ? <Particle PIXI={{ ...PIXI, particles }} /> : null}
        </Particles>
      )}
    </PIXILoadable>
  </Box>
)

export default Bubbles
