import React from 'react'
import {
  Modal,
  ModalCloseButton,
  ModalContent,
} from "@chakra-ui/react"
import GuidePage from '../../GuidePage'

const GuideModal = ({ isOpen, onOpen }) => {
  return (
    <Modal size={''} isCentered isOpen={isOpen} onClose={() => onOpen(false)}>
      <ModalContent borderRadius={'2em'} bg="custom.blue" width="90%">
        <GuidePage index={isOpen} />
        <ModalCloseButton index={10} fontSize="2em" color="white" top="1em" right="1em" />
      </ModalContent>
    </Modal>
  )
}

export default GuideModal
